<template>
  <div id="app">
    <el-card class="table-card">
      <div slot="header">
        盘库清单
      </div>
      <el-form>
        <el-form-item label="搜索">
          <el-col :span="18">
            <el-input v-model="searchContent" placeholder="输入原料名称或订单编号"></el-input>
          </el-col>
          <el-button type="primary" plain @click="fuzzySearch()">搜索</el-button>
        </el-form-item>
      </el-form>
      <el-table :data="searchVisible?temp_list:items" height="250" stripe border>
        <el-table-column prop="oid" label="订单编号"></el-table-column>
        <el-table-column prop="mid" label="原料编号"></el-table-column>
        <el-table-column prop="name" label="原料名称"></el-table-column>
        <el-table-column label="详情">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="seeDetail(scope.row, scope.$index)">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <el-button type="primary" plain @click="countInv()">盘库</el-button>
    <el-button type="primary" plain @click="back_home">返回</el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [
        { oid: '0001', mid: '001', name: '鹅' },
        { oid: '0002', mid: '001', name: '鹅' },
        { oid: '0003', mid: '001', name: '鹅' },
        { oid: '0004', mid: '001', name: '鹅' },
        { oid: '0005', mid: '001', name: '鹅' }
      ],
      temp_list: [],
      cur_item: {},
      orig_item: {},
      curIndex: 1,
      delVisible: false,
      addVisible: false,
      searchVisible: false,
      searchContent: '',
      addFlag: true
    }
  },
  methods: {
    back_home() {
      location.assign('../index.html')
    },
    fuzzySearch() {
      this.temp_list = this.items.slice(0)
      this.searchVisible = true
      for(var i = 0; i < this.temp_list.length; i++) {
        if(this.temp_list[i].name.indexOf(this.searchContent) == -1 && this.temp_list[i].oid.indexOf(this.searchContent) == -1) {
          this.temp_list.splice(i, 1),
          i--
        }
      }
    },
    countInv() {
      this.$notify({
        title: 'Success',
        message: 'success',
        duration: 6000
      })
    },
    seeDetail(row, curIndex) {
      this.$notify({
        title: curIndex,
        message: row,
        duration: 6000
      })
    }
  }
}
</script>

<style>
.table-card {
  width: 800px;
  margin: 100px auto;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 20px;
}
</style>
